import React, {
  useMemo,
  useEffect,
  useState,
  useRef,
  useCallback,
} from "react";
import {
  MapContainer,
  TileLayer,
  ScaleControl,
  LayersControl,
  FeatureGroup,
  Marker,
  Popup,
  useMapEvents,
} from "react-leaflet";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";

const MarkerComponent = ({
  info,
  markers,
  auth,
  setMarkers,
  loading,
  setLoading,
}) => {
  const [position, setPosition] = useState([info.lat, info.lng]);
  const [draggable, setDraggable] = useState(true);
  const markerRef = useRef(null);
  const popupWrapperRef = useRef(null);
  const popupRef = useRef(null);

  const [editInfo, setEditInfo] = useState({
    name: info.name,
    job: info.job,
  });

  const onChangeText = async (type, e) => {
    const cp = { ...editInfo };
    cp[type] = e.target.value;
    setEditInfo(cp);
  };

  useEffect(() => {
    /**
     * Alert if clicked on outside of element
     */
    async function handleClickOutside(event) {
      if (popupRef.current && !popupRef.current.contains(event.target)) {
        await popupWrapperRef.current._closeButton.click();
        await toggleEdit(false);
      }
    }
    // Bind the event listener
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      // Unbind the event listener on clean up
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [popupRef]);

  const eventHandlers = useMemo(
    () => ({
      async dragend() {
        const marker = markerRef.current;
        if (marker != null) {
          await setLoading(true);

          setTimeout(async () => {
            await setPosition(marker.getLatLng());
            const cp = JSON.parse(localStorage.getItem("markers"));
            cp[info.id] = {
              ...cp[info.id],
              lat: marker.getLatLng().lat,
              lng: marker.getLatLng().lng,
            };
            await setMarkers(cp);
            console.log(".");
            await setLoading(false);
          }, 100);
        }
      },
    }),
    []
  );

  /**
   *   
   */
  const toggleEdit = async (flag) => {
    const cp = JSON.parse(localStorage.getItem("markers"));

    cp[info.id] = {
      ...cp[info.id],
      isEdit: flag,
    };
    await setMarkers(cp);
  };

  /**
   *   
   */
  const saveMarkerInfo = async () => {
    const cp = JSON.parse(localStorage.getItem("markers"));
    cp[info.id] = {
      ...cp[info.id],
      name: editInfo.name,
      job: editInfo.job,
      isEdit: false,
    };
    await setMarkers(cp);
    await popupWrapperRef.current._closeButton.click();
  };

  /**
   *  
   *  */
  const removeMarker = (id) => {
    const cp = { ...markers };
    delete cp[info.id];
    setMarkers(cp);
  };

  return (
    <Marker
      draggable={draggable}
      ref={markerRef}
      eventHandlers={eventHandlers}
      position={position}
      style={{ color: "red" }}>
      {auth === "user" && (
        <Popup minWidth={230} ref={popupWrapperRef}>
          <div ref={popupRef}>
            <h2 style={{ margin: "0" }}>{info.name}</h2>
            <hr></hr>
            <p
              style={{
                margin: "0",
                marginBottom: "8px",
                fontSize: "15px",
              }}>
              <b></b> : {info.job}
            </p>
            <p
              style={{
                margin: "0",
                marginBottom: "8px",
                fontSize: "15px",
              }}>
              <b>(lat)</b> : {Math.floor(info.lat)}
            </p>
            <p
              style={{
                margin: "0",
                marginBottom: "8px",
                fontSize: "15px",
              }}>
              <b>(lng)</b> : {Math.floor(info.lng)}
            </p>
          </div>
        </Popup>
      )}
      {auth === "admin" && !info.isEdit ? (
        <Popup minWidth={230} ref={popupWrapperRef}>
          <div ref={popupRef}>
            <h2 style={{ margin: "0" }}>{info.name}</h2>
            <hr></hr>
            <p
              style={{
                margin: "0",
                marginBottom: "8px",
                fontSize: "15px",
              }}>
              <b></b> : {info.job}
            </p>
            <p
              style={{
                margin: "0",
                marginBottom: "8px",
                fontSize: "15px",
              }}>
              <b>(lat)</b> : {Math.floor(info.lat)}
            </p>
            <p
              style={{
                margin: "0",
                marginBottom: "8px",
                fontSize: "15px",
              }}>
              <b>(lng)</b> : {Math.floor(info.lng)}
            </p>
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "spacebetween",
                gap: "4px",
              }}>
              <Button
                onClick={() => removeMarker(info.id)}
                style={{
                  width: "100%",
                  height: "35px",
                  marginTop: "10px",
                }}
                variant='outlined'>
                
              </Button>
              <Button
                onClick={() => toggleEdit(true)}
                style={{
                  width: "100%",
                  height: "35px",
                  marginTop: "10px",
                }}
                variant='outlined'>
                 
              </Button>
            </div>
          </div>
        </Popup>
      ) : (
        <Popup minWidth={230} ref={popupWrapperRef}>
          <div ref={popupRef}>
            <div style={{ marginBottom: "6px" }}>
              <h2 style={{ margin: "0", marginBottom: "4px" }}></h2>
              <TextField
                required
                id='standard-required'
                label='name'
                defaultValue={editInfo.name}
                onChange={(e) => onChangeText("name", e)}
                variant='standard'
                hiddenLabel
              />
            </div>
            <div style={{ marginBottom: "6px" }}>
              <TextField
                required
                id='standard-required'
                label='job'
                defaultValue={editInfo.job}
                onChange={(e) => onChangeText("job", e)}
                variant='standard'
                hiddenLabel
              />
            </div>

            <div
              style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "spacebetween",
                gap: "4px",
              }}>
              <Button
                onClick={() => saveMarkerInfo(info.id)}
                style={{
                  width: "100%",
                  height: "35px",
                  marginTop: "10px",
                }}
                variant='outlined'>
                
              </Button>
            </div>
          </div>
        </Popup>
      )}
    </Marker>
  );
};

export default MarkerComponent;
