const ModeButton = ({ auth, toggleAuth }) => {
  return (
    <div
      style={{
        position: "absolute",
        width: "auto",
        height: "auto",
        borderRadius: "40px",
        backgroundColor: "red",
        right: "0",
        bottom: "0",
        zIndex: "2005",
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        alignItems: "center",
        marginBottom: "15px",
        marginRight: "15px",
        cursor: "pointer",
      }}>
      {auth === "user" ? (
        <Fab
          variant='extended'
          color={"warning"}
          onClick={() => toggleAuth("admin")}>
          <FaceRoundedIcon sx={{ mr: 1 }} />
          user
        </Fab>
      ) : (
        <Fab
          variant='extended'
          color='primary'
          onClick={() => toggleAuth("user")}>
          <VpnKeyRoundedIcon sx={{ mr: 1 }} />
          admin
        </Fab>
      )}
    </div>
  );
};

export default ModeButton;
