import React, { useState, useEffect } from "react";
import Maps from "screens/Maps";
import Menubar from "components/navs/Menubar";
import Fab from "@mui/material/Fab";
import VpnKeyRoundedIcon from "@mui/icons-material/VpnKeyRounded";
import FaceRoundedIcon from "@mui/icons-material/FaceRounded";
import ModeButton from "components/button/ModeButton";
import EditBackground from "components/popup/EditBackground";
import { CircularProgress } from "@mui/material";
const MainScreen = () => {
  const [map, setMap] = useState(null);
  const [loading, setLoading] = useState(false);
  const [auth, setAuth] = useState(
    localStorage.getItem("auth") ? localStorage.getItem("auth") : "user"
  );
  const [mode, setMode] = useState(
    localStorage.getItem("mode") ? localStorage.getItem("mode") : "view"
  );
  const [isSticky, setIsSticky] = useState(
    localStorage.getItem("isSticky") ? localStorage.getItem("isSticky") : "true"
  );
  const [clickLock, setClickLock] = useState(
    localStorage.getItem("clickLock")
      ? localStorage.getItem("clickLock")
      : "true"
  );

  useEffect(() => {
    refreshStorage();
    setAuth("admin");
    setMode("view");
  }, []);

  /**
   * localStorage 
   */
  const refreshStorage = () => {
    localStorage.clear();
  };

  const toggleAuth = (text) => {
    setAuth(text);
    if (text === "user") {
      setMode("view");
      setClickLock("true");
    }
  };

  const updateMode = async (text) => {
    if (text === "create") {
      setMode(text);
      setClickLock("false");
    } else setMode(text);
  };

  const toggleSticky = async () => {
    setIsSticky(isSticky === "true" ? "false" : "true");
  };

  useEffect(() => {
    localStorage.setItem("isSticky", isSticky ? "true" : "false");
  }, [isSticky]);

  useEffect(() => {
    localStorage.setItem("clickLock", clickLock);
  }, [clickLock]);

  useEffect(() => {
    localStorage.setItem("mode", mode);
  }, [mode]);

  useEffect(() => {
    localStorage.setItem("auth", auth);
  }, [auth]);

  return (
    <div className='App'>
      {map && (
        <Menubar
          map={map}
          auth={auth}
          mode={mode}
          setMode={setMode}
          updateMode={updateMode}
          isSticky={isSticky}
          toggleSticky={toggleSticky}
        />
      )}
      <ModeButton auth={auth} toggleAuth={toggleAuth} />
      {/*   . */}
      <Maps
        setMap={setMap}
        auth={auth}
        mode={mode}
        updateMode={updateMode}
        clickLock={clickLock}
        setClickLock={setClickLock}
        isSticky={isSticky}
        loading={loading}
        setLoading={setLoading}
      />
      {loading && (
        <div
          style={{
            position: "absolute",
            zIndex: "30000",
            backgroundColor: "black",
            opacity: "0.5",
            top: "0",
            left: "0",
            height: "100%",
            width: "100%",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}>
          <CircularProgress size={60} />
        </div>
      )}
    </div>
  );
};

export default MainScreen;
