import React, {
  useMemo,
  useEffect,
  useState,
  useRef,
  useCallback,
} from "react";
import {
  MapContainer,
  TileLayer,
  ScaleControl,
  LayersControl,
  FeatureGroup,
  Marker,
  Popup,
  useMapEvents,
} from "react-leaflet";
import Button from "@mui/material/Button";
import MarkerComponent from "components/marker/MarkerComponent";

import { v4 as uuidv4 } from "uuid";

const Map = ({
  setMap,
  clickLock,
  mode,
  auth,
  setClickLock,
  updateMode,
  setLoading,
}) => {
  const mapRef = useRef();
  const [markers, setMarkers] = useState(
    JSON.parse(localStorage.getItem("markers"))
      ? JSON.parse(localStorage.getItem("markers"))
      : {}
  );

  useEffect(() => {
    saveMarkers();
  }, [markers]);

  const saveMarkers = async () => {
    await localStorage.setItem("markers", JSON.stringify(markers));
  };

  function MyComponent() {
    const map = useMapEvents({
      click: async (e) => {
        if (
          localStorage.getItem("clickLock") === "false" &&
          localStorage.getItem("mode") === "create"
        ) {
          const newMarker = {
            id: uuidv4(),
            name: "anonymous",
            job: " ",
            lat: e.latlng.lat,
            lng: e.latlng.lng,
            image: "",
            room: 0,
            isDraggable: false,
            isEdit: false,
            isDeleted: false,
          };

          const _markers = await { ...markers };
          _markers[newMarker.id] = newMarker;
          await setClickLock("true");
          await setMarkers(_markers);
          console.log(" .");
          updateMode("view");
          /**
           * 1.   
           * 2.    
           * 3.
           */
        }
      },
    });
    return null;
  }

  const map = useMemo(() => {
    return (
      <MapContainer
        doubleClickZoom={false}
        ref={mapRef}
        id='mapId'
        zoom={2}
        minZoom={2}
        maxZoom={4}
        maxBounds={[
          [300, -300],
          [-300, 300],
        ]}
        minBounds={[
          [300, -300],
          [-300, 300],
        ]}
        center={{ lat: -10.8817, lng: -50.4253 }}
        whenCreated={setMap}>
        <MyComponent />
        <TileLayer url='/grandview/image/drawing_b/{z}_{x}_{y}.png' />

        <ScaleControl />

        <LayersControl>
          <FeatureGroup>
            {Object.entries(markers).map((element, index) => {
              return (
                <MarkerComponent
                  key={element[0]}
                  info={element[1]}
                  markers={markers}
                  auth={auth}
                  setMarkers={setMarkers}
                  setLoading={setLoading}
                />
              );
            })}
          </FeatureGroup>
        </LayersControl>
      </MapContainer>
    );
  }, [markers]);

  return map;
};

export default Map;
